@import '../common/styles/sass/variable';


.projectsBlock {
  background-color: $bgColor;
  color: $textColor;
  padding: 50px 0;
  text-align: center;

  .projectsContainer {
    flex-direction: column;
    justify-content: flex-start;
    flex-wrap: nowrap;

    .projects {
      width: 100%;
      display: flex;
      justify-content: space-between;
      flex-wrap: wrap;
    }
  }
}





