@import '../common/styles/sass/variable';

.contactsBlock {
  background-color: $bgSecondColor;
  color: $textColor;
  text-align: center;
  padding: 50px 0;

  .contactsContainer {
    display: block;

    .title {
      display: inline-flex;
    }

    .form {
      display: flex;
      flex-direction: column;
      border: 1px solid;
      margin: 0 auto 40px;
      width: 65%;
    }

  }
}






