@import "../common/styles/sass/variable";

.skillsBlock {
  /*height: 100vh;*/
  background-color: #1e2326;
  color: $textColor;
  padding: 50px 0;

  .skillsContainer {
    flex-direction: column;
    justify-content: flex-start;
    flex-wrap: nowrap;

    .skills {
      width: 100%;
      display: flex;
      justify-content: space-between;
      flex-wrap: wrap;

      .title {
        display: inline-flex;
      }
    }
  }
}

